.Button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    border: none;
    font-size: 18px;
    border-radius: 6px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    padding: 8px 8px;
    color: white;
    background-color: #4CAF50;
    cursor: pointer;
}

.disabled {
    color: #9E9E9E;
    background-color: #A5D6A7;
    cursor: not-allowed;
}




